import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ExamplesPage } from '../examples/examples';
import { SponsorPage } from '../sponsor/sponsor';
import { DosimPage } from '../dosim/dosim';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  content : any = 'Loading ...';
  banner : any = 'https://mysmilesim.dental/server/sponsorbanner.jpg';
  num : any = 0;

  constructor(public navCtrl: NavController, private connect : Connect) {

  }

  ionViewDidEnter()
  { let uniq = window.localStorage.getItem('uniq');
    if (!uniq) {
     let d  = new Date();
     let n1 = Math.floor(Math.random()*26)+65;
     let n2 = Math.floor(Math.random()*26)+65;
     uniq=String.fromCharCode(n1)+ d.getTime().toString() + String.fromCharCode(n2);
     window.localStorage.setItem('uniq',uniq);
    }

   let that=this;
       let url = 'home.php?me='+uniq;
        this.connect.getList(url).subscribe(
         data => {
           that.content = data.content;
           that.banner = data.banner;
           that.num = data.num; 
         },
         err => {
         }
        ); 
  }

  examples() {
console.log('here');

    this.navCtrl.push(ExamplesPage);
  }

  showSponsor() {
    this.navCtrl.push(SponsorPage);
  }

  startup() {
    this.navCtrl.push(DosimPage);
  }

}



